.wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 50px 10px 10px;
    gap: 10px;
    margin: auto;
}

.li:not(:last-child) {
    margin-bottom: 10px;
}

.link {
    color: black;
    text-decoration: none;
    font-weight: 500;
}

.link:hover {
    color: #ab47bc;
}